  Template.room.onCreated(function () {
    var self;
    self = this;
    self.autorun(function () {
      return self.subscribe('livechat:visitorRoom', visitor.getToken(), function () {
        var room;
        room = ChatRoom.findOne();
        if (room != null && room.open) {
          visitor.setRoom(room._id);
          return RoomHistoryManager.getMoreIfIsEmpty(room._id);
        }
      });
    });


    self.subscribe('settings', ['Livechat_title', 'Livechat_title_color']);
    return self.atBottom = true;
  });
